import React, { Component } from 'react';
import { Modal } from 'react-bootstrap';
import Select from 'react-select';
import { currencyList } from '../../../../Services/Utils/utils';
//import { baseUrl } from '../../../../Assets/AssetsHelper';

class AddProductModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // descriptionEditable : false,
            // addTagButton : true,
            //tagInputValue : ''
        };
    }
    addTagValue = (value) => {

    }

    handleClose = () => {
        this.props.handleAddProCallback(false)
    };

    render() {
        const { addProModalToggle } = this.props
        //const {tagString } = this.state

        const currencyData = currencyList && currencyList.length > 0 ? currencyList.map((currency) =>
        ({
            value: currency.code,
            label: <div className="csp_wrap" title={currency.namePlural} data-abbreviation={currency.code} ><span>{currency.name} - {currency.symbolNative} </span></div>
        })
        ) : null
        return <Modal show={addProModalToggle} onHide={this.handleClose}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Add Product</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="addPro_modal mx-xl-4 px-0 px-lg-4 mx-0">
                    <div className="row">
                        <div className="col-sm-6">
                            <div className="border rounded-3 h-100px">

                            </div>
                        </div>
                        <div className="col-sm-6">
                            <div className="mb-2 mb-xl-3">
                                <label className="text-muted mb-1">Product Name</label>
                                <input type="text" name="pname" className="form-control" placeholder="Enter Product Name" />
                            </div>
                            <div className="row mb-2 mb-xl-3">
                                <div className="col">
                                    <label className="text-muted mb-1">Price</label>
                                    <div className="input-group">
                                        <span className="input-group-text p-0">
                                            <Select id="" className="selectpicker countrypicker"
                                                classNamePrefix="selectpicker"
                                                isLoading={false}
                                                isClearable={false}
                                                isSearchable={false}
                                                name="country"
                                                options={currencyData} />
                                        </span>
                                        <input type="text" className="form-control" placeholder="$0.00" />
                                    </div>
                                </div>
                                <div className="col">
                                    <label className="text-muted mb-1">Discount Price</label>
                                    <div className="input-group">
                                        <span className="input-group-text">$</span>
                                        <input type="text" className="form-control" placeholder="$0.00" />
                                    </div>
                                </div>
                            </div>
                            <div className="mb-2 mb-xl-3">
                                <label className="text-muted mb-1">Product URL</label>
                                <input type="text" name="pname" className="form-control" placeholder="https://www.website.com" />
                            </div>
                            <div className="row mb-2 mb-xl-3">
                                <div className="col">
                                    <label className="text-muted mb-1">Product SKU</label>
                                    <input type="text" className="form-control" placeholder="Enter product sku" />
                                </div>
                                <div className="col">
                                    <label className="text-muted mb-1">Product Id</label>
                                    <input type="text" className="form-control" placeholder="Enter product Id" />
                                </div>
                            </div>
                            <div className="mb-2 mb-xl-3">
                                <label className="text-muted mb-1">Category</label>
                                <input type="text" name="pname" className="form-control" placeholder="Category Name" />
                            </div>
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer className="p-3">
                <div className="mx-xl-4 px-2 px-lg-4 mx-0">
                    <button type="button" className="btn btn-secondary btn-sm btn-min-w me-3" onClick={this.handleClose}>Cancel</button>
                    <button type="button" className="btn btn-primary btn-sm btn-min-w">Submit</button>
                </div>
            </Modal.Footer>
        </Modal>

    }
}

export default AddProductModal;